section {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    margin: 1rem 0 2rem 0;
    color: var(--black);
}

.box-skill {
    background-color: var(--pink);
    padding: 2rem;
    width: 250px;
    border-radius: 20px;
    box-shadow: 4px 4px 4px var(--black);
}

.box-skill:hover {
    background-color: var(--lilac);
}

.box-skill h3 {
    font-family: 'Handlee', cursive;
    text-align: center;
    margin-bottom: 1rem;
}

.box-skill p {
    text-align: center;
    font-family: 'Poppins', sans-serif;
}