footer {
    width: 100%;
    background-image: url(../../img/footer.svg);
    background-size: cover;
    height: 180px;
}

.box-icons {
    display: flex;
    justify-content: center;
}

.icons {
    color: var(--dark-blue);
    border-radius: 50%;
    width: 60px;
    height: 60px;
    font-size: 40px;
    background-color: var(--grey);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 20px;
}