@import url('https://fonts.googleapis.com/css2?family=Handlee&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

:root {
  --dark-blue: #011487;
  --lilac: #A37FFF;
  --pink: #CA9AFE;
  --black: #020829;
  --white: #F0EAF8;
  --grey: #DDD7FD;
}

body {
  font-size: 16px;
  font-family: 'Poppins', sans-serif;
}
.jobs, .studies{
  margin: 1rem;
  width: 40rem;
}
.jobs-container, .studies-container{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.jobs-title, .studies-title {
  font-family: 'Handlee', cursive;
  font-size: 20px;
  font-weight: bold;
  color: var(--black);
  background-color: var(--lilac);
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 4rem;
  padding: 0.5rem;
}

.jobs-info,
.jobs-btn,.studies-btn {
  font-family: 'Poppins', sans-serif;
  margin-top: 10px;
  padding: 1rem;
  color: var(--black);
}

.jobs-btn, .studies-btn{
font-weight: 600;
border-radius: 20px;
text-align: center;
}

.container{
  margin: 2rem auto;
  width: 70%;
  padding: 3rem;
  background-color: var(--pink);
  color: var(--black);
  border-radius: 10px;
  text-align: justify;
}

.container h3{
  text-align: center;
  margin-bottom: 1rem;
}