section {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    margin: 1rem 0;
    color: var(--black);
}

.text {
    width: 50%;
}

.text h3 {
    font-family: 'Handlee', cursive;
    font-weight: bold;
    font-size: 28px;
}

.text p {
    font-family: 'Poppins', sans-serif;
    font-size: 20px;
    text-align: justify;
    margin-top: 20px;
}

.photo {
    width: 200px;
}